<template>
    <div id="blog-form">
<!--		Starts at 42:47	-->
			<a @click.prevent="scrollToTop" id="btnscrollTop">
				<font-awesome-icon class="color-icon-up" icon="chevron-circle-up"/>
			</a>		
			
			<div class="text-2xl mb-4 md:mb-12 font-bold flex justify-center items-center our-loc">Polymorphism</div>
      
      <section>
        <div class="vl-solmn-inner">
          <div class="wb-wrapper">
            <div class="container mx-auto px-9 lg:px-69 cont-contf">
              <h1>The primary usage of Polymorphism in industry (object-oriented programming theory) is the ability of objects belonging to different types to respond to method, field or property calls of the same name, each one according to an appropriate type specific behaivior. The programmer does not have to know the exact type of the object in advance and so the exact behavior is determined at run-time.</h1>
              <br>
              <h1>Lets take a look at this program.</h1>
              
<pre class="result notranslate">
<span class="b">class</span> <span class="y">Employee</span>  {

  <span class="b">protected</span>:
   <span class="g">string</span> name;
   <span class="b">double</span> pay;
  
  <span class="b">public</span>:
   <span class="y">Employee</span>() : name{ "" }, pay{ 0.0 } {}
   <span class="y">Employee</span>(<span class="g">string</span> name, <span class="b">double</span> pay) : name{ name }, pay{ pay } {}
  
  <span class="b">public</span>:
    <span class="g">string</span> getName(){
      <span class="p">return</span> name;
    }
    
    <span class="b">double</span> getPay(){
      <span class="p">return</span> pay;
    }
    
    <span class="b">void</span> setName(<span class="g">string</span> name){
      <span class="b">this</span>->name = name;
    }
    
    <span class="b">void</span> setPay(<span class="b">double</span> pay){
      <span class="b">this</span>->pay = pay;
    }
};

<span class="b">class</span> <span class="y">Manager</span> : <span class="y">Employee</span>{

  <span class="b">private</span>:
   <span class="b">bool</span> salaried;
  
  <span class="b">public</span>:
   <span class="y">Manager</span>(<span class="g">string</span> name, <span class="b">double</span> pay, <span class="b">bool</span> isSalaried) : <span class="y">Employee</span>(name, pay), salaried{ isSalaried } {}
  
  <span class="b">public</span>:    
    <span class="b">bool</span> getSalaried(){
      <span class="p">return</span> salaried;
    }
    
    <span class="b">double</span> grossPay(<span class="b">int</span> hours){
      <span class="p">if</span>(salaried){
       <span class="p">return</span> pay;
      }<span class="p">else</span>{
       <span class="p">return</span> pay * hours;
      }
    }
    
};

</pre>  
<h1>We have an Employee class which is our Base Class and a Manager class which is derived class. What we wanna be able to do is create a collection of objects be able to call a function on this objects and have a compiler figure out at runtime which type of objects it's calling a function for. We will be using a vector to store an objects. </h1>              
         
<pre class="result notranslate">
<span class="b">int</span> <span class="y">main</span>()  {
  <span class="g">vector</span>&#60;<span class="y">Employee</span>&#62; <span class="m">emps</span>;
  <span class="y">Employee</span> <span class="m">emp1</span>(<span class="t">"Jones"</span>, 122.2);
  <span class="y">Manager</span> <span class="m">mn1</span>(<span class="t">"Bread"</span>, 1200, <span class="b">true</span>);
  
  <span class="m">emps</span>.<span class="y">push_back</span>(<span class="m">emp1</span>);
  <span class="m">emps</span>.<span class="y">push_back</span>(<span class="m">mn1</span>);
};
  </pre>
              
<h1>I wanna be now able to run a loop where for each object in the vector i want to call a grossPay function for that object.</h1>
              
<pre class="result notranslate">
<span class="b">for</span>(<span class="b">int</span> i=0; i&#60;<span class="m">emps</span>.<span class="y">size</span>(); ++i){
  cout&#60;&#60;<span class="m">emps</span>[i].<span class="y">grossPay</span>(40)&#60;&#60;endl;
}
</pre>
<h1>The problem occure because the compiler does not know which type of growsPay() function to call.</h1>
              <br>
              <hr>
<div class="text-2xl mb-4 md:mb-12 font-bold flex justify-center items-center our-loc">Implementing the Polymorphism</div>
<h1>We can implement pointers, use pointers to try to get the compiler to figure out what type of function to call.</h1>
<pre class="result notranslate">
<span class="b">int</span> <span class="y">main</span>()  {
 <span class="y">Employee</span> <span class="m">emp1</span>(<span class="t">"Jones"</span>, 122.2);
 <span class="y">Manager</span> <span class="m">mn1</span>(<span class="t">"Bread"</span>, 1200, <span class="b">true</span>);

 <span class="y">Employee</span> *<span class="m">empPtr</span>;
 <span class="m">empPtr</span> = &amp;<span class="m">emp1</span>;
 cout&#60;&#60;"Name: "&#60;&#60;<span class="m">empPtr</span>-><span class="y">getName</span>()&#60;&#60;endl;
 cout&#60;&#60;"Pay: "&#60;&#60;<span class="m">empPtr</span>-><span class="y">grossPay</span>(40)&#60;&#60;endl;
 
 <span class="m">empPtr</span> = &amp;<span class="m">mn1</span>;
 cout&#60;&#60;"Name: "&#60;&#60;<span class="m">empPtr</span>-><span class="y">getName</span>()&#60;&#60;endl;
 cout&#60;&#60;"Pay: "&#60;&#60;<span class="m">empPtr</span>-><span class="y">grossPay</span>(40)&#60;&#60;endl;
}
</pre>
              
<h1>The problem stays the same, because the compiler is loking not at object that is stored in our variable but instead a data type of variable itself. We still have not solve the problem, but what we need to do is to modify the definition of the Employee grossPay() function and the Manager grossPay() function to make it a virtual function.</h1>
              
<pre class="result notranslate">
<span class="b">virtual</span> <span class="b">double</span> grossPay(<span class="b">int</span> hours){
  <span class="p">return</span> pay * hours;
}
</pre>
<h1>Now the compiler will look at that function know that that it is virtual and determine which code to run base on the type of object that is stored in variable rather than just the data type that variable was declared as. The Base Class function needs to be a virtual function (recommended).</h1>
 </div>
