<template>
    <div id="blog-form">
<!--		Starts at 1:03:55	-->
			<a @click.prevent="scrollToTop" id="btnscrollTop">
				<font-awesome-icon class="color-icon-up" icon="chevron-circle-up"/>
			</a>		
			
			<div class="text-2xl mb-4 md:mb-12 font-bold flex justify-center items-center our-loc">Polymorphism using Abstract Classes</div>
      
      <section>
        <div class="vl-solmn-inner">
          <div class="wb-wrapper">
            <div class="container mx-auto px-9 lg:px-69 cont-contf">
<h1>We gonna demonstrate Polymorphism using Abstract Classes. We are gonna create an Abstract Animal class and than derive to classes Dog and Cat to show technic involved in pure virtual functions and abstract classes. All we have is an Animal class with one pure virtual function talk()</h1>
              
<pre class="result notranslate">
<span class="b">class</span> <span class="y">Animal</span>  {
<span class="b">public</span>:
 <span class="b">virtual</span> <span class="b">void</span> talk() = 0;
};
</pre>
              
<h1>Each derived function is going to have implement its own version of talk() where polymorphism comes because the compiler is going to anderstand that Dogs version of talk will be different that the Cats function.</h1>       
              
<pre class="result notranslate">
<span class="b">class</span> <span class="y">Dog</span> : <span class="b">public</span> <span class="y">Animal</span> {
<span class="b">public</span>:
 <span class="b">virtual</span> <span class="b">void</span> talk(){
  cout&#60;&#60;"Dog talk!"&#60;&#60;endl;
 }
};

<span class="b">class</span> <span class="y">Cat</span> : <span class="b">public</span> <span class="y">Animal</span> {
<span class="b">public</span>:
 <span class="b">virtual</span> <span class="b">void</span> talk(){
  cout&#60;&#60;"Cat talk!"&#60;&#60;endl;
 }
};

<span class="b">int</span> <span class="y">main</span>(){
 <span class="y">Dog</span> <span class="m">fido</span>;
 <span class="y">Cat</span> <span class="m">kitty</span>;
 
 <span class="m">fido</span>.<span class="y">talk</span>();
 <span class="m">kitty</span>.<span class="y">talk</span>();
 
 <span class="p">return</span> 0;
}
</pre>
              
              
<h1>So here is another example this time using Abstract classes where we can have a single function talk() and the compiler will figure out how to use it based on the object that its connected to. And because its a pure virtual function each class had to implement at least the talk() function.</h1>
              

 </div>
            </div>
  </div>
