            <div class="container mx-auto px-9 lg:px-69 cont-contf">
              <h1>An Abstract class is a class that has a function declarations but not executable code in them. Some times we want to create a class that can not be instantiated but instead is used as a base class in a model of all the derived classes in chane of inheritance. Class that can not be instantiated is called an abstract class.</h1>
              <br>
              <h1>Lets create an Abstract Class. Lets say we are creating a geometrical program of some type, a program that draws shapes. A class we might wanna use is a base for all of our other classes might be a shape class. We wont be instantiate a base class Shape because wont have a particular shape. Instead we want to derive classes from the Shape class such as Circle, Rectangle, Square. What we can do is to make a class Abstract, and we do that by creating a set of methods and making them all pure virtual methods. And the way you do that is you provide an initializer for the virtual method.</h1>
              
<pre class="result notranslate">
<span class="b">class</span> <span class="y">Shape</span>  {
 <span class="b">virtual</span> <span class="b">void</span> setX(<span class="b">int</span> xcor) = 0;
 <span class="b">virtual</span> <span class="b">void</span> setY(<span class="b">int</span> ycor) = 0;
 <span class="b">virtual</span> <span class="b">int</span> getX() <span class="b">const</span> = 0;
 <span class="b">virtual</span> <span class="b">int</span> getY() <span class="b">const</span> = 0;
 <span class="b">virtual</span> <span class="b">void</span> draw() <span class="b">const</span> = 0;
};

</pre>  
<h1>Now a Shape class becomes abstract, by that we mean that we can not instantiate a class Shape, and if we try to we just get a compiler error. What we wanna do that is to create a derive class that is based on this Shape class. For example class Circle. </h1>              
<pre class="result notranslate">
<span class="b">class</span> <span class="y">Circle</span> : <span class="b">public</span> <span class="y">Shape</span> {
 
};
</pre>
<h1>In order for a Circle not to become another abstract class we have to implement all of the pure virtual function that in a base class Shape.</h1>
<pre class="result notranslate">
<span class="b">class</span> <span class="y">Circle</span> : <span class="b">public</span> <span class="y">Shape</span> {
<span class="b">private</span>:
 <span class="b">int</span> x, y, radius;
<span class="b">public</span>:
 <span class="y">Circle</span>(<span class="b">int</span> xcor, <span class="b">int</span> ycor, <span class="b">int</span> r): x{xcor}, y{ycor}, radius{r}{}
 
<span class="b">public</span>:
 <span class="b">virtual</span> <span class="b">void</span> setX(<span class="b">int</span> xcor){
  x = xcor;
 }
 
 <span class="b">virtual</span> <span class="b">void</span> setY(<span class="b">int</span> ycor){
  y = ycor;
 }
 
 <span class="b">void</span> setRadius(<span class="b">int</span> r){
  radius = r;
 }
 
 <span class="b">virtual</span> <span class="b">int</span> getX() <span class="b">const</span>{
  <span class="p">return</span> x;
 }
 
 <span class="b">virtual</span> <span class="b">int</span> getY() <span class="b">const</span>{
  <span class="p">return</span> y;
 }
 
 <span class="b">int</span> getRadius() <span class="b">const</span>{
  <span class="p">return</span> radius;
 }
 
 <span class="b">virtual</span> <span class="b">void</span> draw() <span class="b">const</span>{
  // Drawing a Circle at getX(), getY(), getRadius().
 }
};

<span class="b">int </span> <span class="y">main</span>(){
 <span class="y">Circle</span> <span class="m">c1</span>(2, 3, 5);
 <span class="m">c1</span>.<span class="y">draw</span>();
 
 <span class="p">return</span> 0;
}
</pre>
<h1>We have to implement all of the functions that was defined as pure virtual. That is how you create and use a abstract Shape class.</h1>
