			<div class="text-2xl mb-4 md:mb-12 font-bold flex justify-center items-center our-loc">Generic Programming</div>
      
      <div class="text-2xl mb-4 md:mb-12 font-bold flex our-loc">1. Generic Functions</div>
      
      <section>
        <div class="vl-solmn-inner">
          <div class="wb-wrapper">
            <div class="container mx-auto px-9 lg:px-69 cont-contf">
<h1>In generic programming we can write code to perform certain tasks without specifying the data type (for example a return value of a function), but rather we can determine the data type at run time so that we can create more flexible programs.</h1>
<h1>Lets create a simple array.</h1>
              
<pre class="result notranslate">
<span class="b">int</span> <span class="y">main</span>()  {
 <span class="b">const</span> <span class="b">int</span> size{10};
 <span class="b">int</span> <span class="m">numbers</span>[size];
 <span class="b">for</span>(<span class="b">int</span> i=0; i&#60;size; ++i){
  <span class="m">numbers</span>[i] = i+1;
 }
 <span class="p">return</span> 0;
}
</pre>
              
<h1>Lets create a function that will display the array.</h1>       
              
<pre class="result notranslate">
<span class="lg">&#35;include &#60;iostream&#62;</span>

<span class="b">using</span> <span class="b">namespace</span> std;

<span class="b">void</span> display(<span class="b">int</span> arr[], <span class="b">int</span> size){
 <span class="b">for</span>(<span class="b">int</span> i=0; i&#60;size; ++i){
  cout&#60;&#60;arr[i]&#60;&#60;" ";
 }
 cout&#60;&#60;endl;
}

<span class="b">int</span> <span class="y">main</span>()  {
 <span class="b">const</span> <span class="b">int</span> <span class="m">size</span>{10};
 <span class="b">int</span> <span class="m">numbers</span>[size];
 <span class="b">for</span>(<span class="b">int</span> i=0; i&#60;<span class="m">size</span>; ++i){
  <span class="m">numbers</span>[i] = i+1;
 }
 
 <span class="y">display</span>(<span class="m">numbers</span>, <span class="m">size</span>);
 
 <span class="p">return</span> 0;
}
</pre>
              
              
<h1>Now lets say in the same program we need to create an array of strings say (names). So now we have two arrays, one is array of strings and the other one is an array of integers.</h1>
              
<pre class="result notranslate">
<span class="lg">&#35;include &#60;iostream&#62;</span>
<span class="lg">&#35;include &#60;string&#62;</span>

<span class="b">int</span> <span class="y">main</span>()  {
 <span class="b">const</span> <span class="b">int</span> size{10};
 
 <span class="b">int</span> <span class="m">numbers</span>[size];
 <span class="b">string</span> <span class="m">names</span>[] = {<span class="b">"Jim"</span>, <span class="b">"Fred"</span>, <span class="b">"Jane"</span>, <span class="b">"Bob"</span>, <span class="b">"Mike"</span>, <span class="b">"Terry"</span>, <span class="b">"Mason"</span>};
 
 <span class="b">for</span>(<span class="b">int</span> i=0; i&#60;size; ++i){
  <span class="m">numbers</span>[i] = i+1;
 }
 
 <span class="p">return</span> 0;
}
</pre>
              
<h1>To display an array of strings if i want to use my function display() as is i have to change the data type of the array parameter, so instead my other solution is to create another display() function for displaying strings.</h1>
              
<pre class="result notranslate">
<span class="lg">&#35;include &#60;iostream&#62;</span>

<span class="b">using</span> <span class="b">namespace</span> std;

<span class="b">void</span> display(<span class="b">int</span> arr[], <span class="b">int</span> size){
 <span class="b">for</span>(<span class="b">int</span> i=0; i&#60;size; ++i){
  cout&#60;&#60;arr[i]&#60;&#60;" ";
 }
 cout&#60;&#60;endl;
}

<span class="b">void</span> displayStr(<span class="b">string</span> arr[], <span class="b">int</span> size){
 <span class="b">for</span>(<span class="b">int</span> i=0; i&#60;size; ++i){
  cout&#60;&#60;arr[i]&#60;&#60;" ";
 }
 cout&#60;&#60;endl;
}
</pre>

<h1>These are essentially the same functions that do the very same thing. But we are dealing with different data types we have to create two distinct functions to handle the different data types of the array. We would rather write a generic function single function, that has a template that will allows us to change the data type of the array when we call the function. That is what a generic programming does. Lest see the example.</h1>

<pre class="result notranslate">
<span class="lg">&#35;include &#60;iostream&#62;</span>

<span class="b">using</span> <span class="b">namespace</span> std;

<span class="b">template</span> &#60;<span class="b">typename</span> <span class="b">T</span>&#62; 
<span class="b">void</span> display(<span class="b">T</span> arr[], <span class="b">int</span> size){
 <span class="b">for</span>(<span class="b">int</span> i=0; i&#60;size; ++i){
  cout&#60;&#60;arr[i]&#60;&#60;" ";
 }
 cout&#60;&#60;endl;
}
</pre>
              
<h1>All we have to do is whenever we have a reference to a data type that we want to make flexible or generic we just put a T on its place. At run time the compiler will replace T with the actual data type with a array that is passed to it as an argument. T is just a convention, we can use any any litera. So now our program will only use one function to display our int array and our string array.</h1>
              <hr>
              
              <div class="text-2xl mb-4 md:mb-12 font-bold flex our-loc">2. Generic Classes</div>
              
<h1>No we will increase a complexity a little bit and talk about how to create generic class definitions, so that we can create classes that will work with flexible data type. First thing we have to do is define our class. We will be implementing a very minimal class Stack.</h1>
              
<pre class="result notranslate">
<span class="lg">&#35;include &#60;iostream&#62;</span>

<span class="b">using</span> <span class="b">namespace</span> std;

<span class="b">class</span> <span class="y">Stack</span>{

<span class="b">private</span>:
 <span class="b">int</span> <span class="m">datastore</span>[100];
 <span class="b">int</span> <span class="m">top</span>;
 
<span class="b">public</span>:
 <span class="y">Stack</span>(): top{-1}{}
 
 <span class="b">void</span> push(<span class="b">int</span> num){
  ++<span class="m">top</span>;
  <span class="m">datastore</span>[<span class="m">top</span>] = num;
 }
 
 <span class="b">int</span> pop(){
  <span class="b">int</span> val = <span class="m">datastore</span>[top];
  <span class="m">datastore</span>[top] = 0;
  --<span class="m">top</span>;
  <span class="p">return</span> val;
 }
 
 <span class="b">int</span> peek(){
  <span class="p">return</span> <span class="m">datastore</span>[top];
 }
};

<span class="b">int</span> <span class="y">main</span>(){
 
 <span class="y">Stack</span> <span class="m">numbers</span>;
 
 <span class="m">numbers</span>.<span class="y">push</span>(12);
 <span class="m">numbers</span>.<span class="y">push</span>(25);
 
 <span class="p">return</span> 0;
}
</pre>
              
<h1>The problem is that the data stored is declared as an integer array, so if i want to use some other data type i can't because i have commited the stack to store only integers. So what we want to do is likewise with a functions on a previous lesson use a template so that we can determine the type of the stack and the type of the data that is stored on the stack. Lets make our class Stack Generic.</h1>
              
<pre class="result notranslate">
<span class="lg">&#35;include &#60;iostream&#62;</span>

<span class="b">using</span> <span class="b">namespace</span> std;

<span class="b">template</span> &#60;<span class="b">typename T</span>&#62;
<span class="b">class</span> <span class="y">Stack</span>{

<span class="b">private</span>:
 <span class="b">T</span> <span class="m">datastore</span>[100];
 <span class="b">int</span> <span class="m">top</span>;
 
<span class="b">public</span>:
 <span class="y">Stack</span>(): top{-1}{}
 
 <span class="b">void</span> push(<span class="b">T</span> num){
  ++<span class="m">top</span>;
  <span class="m">datastore</span>[<span class="m">top</span>] = num;
 }
 
 <span class="b">T</span> pop(){
  <span class="b">T</span> val = <span class="m">datastore</span>[top];
  <span class="m">datastore</span>[top] = 0;
  --<span class="m">top</span>;
  <span class="p">return</span> val;
 }
 
 <span class="b">T</span> peek(){
  <span class="p">return</span> <span class="m">datastore</span>[top];
 }
};

<span class="b">int</span> <span class="y">main</span>(){
 
 <span class="y">Stack</span> <span class="m">numbers</span>;
 
 <span class="m">numbers</span>.<span class="y">push</span>(12);
 <span class="m">numbers</span>.<span class="y">push</span>(25);
 
 <span class="p">return</span> 0;
}
</pre>
              
<h1>So now after making our class Stack Generic we have to provide a data type.</h1>
              
<pre class="result notranslate">
<span class="b">int</span> <span class="y">main</span>(){
 
 <span class="y">Stack&#60;<span class="b">int</span>&#62;</span> <span class="m">numbers</span>;
 
 <span class="m">numbers</span>.<span class="y">push</span>(12);
 <span class="m">numbers</span>.<span class="y">push</span>(25);
 
 <span class="y">Stack&#60;<span class="b">double</span>&#62;</span> <span class="m">doubles</span>;
 
 <span class="m">doubles</span>.<span class="y">push</span>(<span class="b">12.2</span>);
 <span class="m">doubles</span>.<span class="y">push</span>(<span class="b">5.02</span>);
 
 <span class="p">return</span> 0;
}
</pre>
              
<h1>So that is how you creare a template class for working with a different data type.</h1>
              
<div class="text-2xl mb-4 md:mb-12 font-bold flex our-loc">3. Class Template Specialization.</div>
<h1>In the previous lesson we demonstrated how to create a template class, but with that implementation we still have a problem when implementing string data type, in a pop() function when we set top value to 0.</h1>
              
<pre class="result notranslate"> 
 <span class="b">T</span> pop(){
  <span class="b">T</span> val = <span class="m">datastore</span>[top];
  <span class="m">datastore</span>[top] = 0;
  --<span class="m">top</span>;
  <span class="p">return</span> val;
 }
</pre>
<h1>When we pushing strings or poping strings of a Stack setting a default value to 0 would be incorrect, sinse the default value for string is empty string. To remedy that situation we have to implement a Template Specialization. We have to begin a new class definition.</h1>
<pre class="result notranslate"> 
<span class="b">template</span>&#60;&#62;
<span class="b">class</span> <span class="y">Stack</span>&#60;<span class="b">string</span>&#62; {
<span class="b">private</span>:
 <span class="b">string</span> <span class="m">datastore</span>[100];
 <span class="b">int</span> <span class="m">top</span>;
 
<span class="b">public</span>:
 <span class="y">Stack</span>(): top{-1}{}
 
 <span class="b">void</span> push(<span class="b">string</span> val){
  ++<span class="m">top</span>;
  <span class="m">datastore</span>[<span class="m">top</span>] = val;
 }
 
 <span class="b">string</span> pop(){
  <span class="b">string</span> val = <span class="m">datastore</span>[top];
  <span class="m">datastore</span>[top] = "";
  --<span class="m">top</span>;
  <span class="p">return</span> val;
 }
 
 <span class="b">string</span> peek(){
  <span class="p">return</span> <span class="m">datastore</span>[top];
 }
};
</pre>
 </div>
            </div>
  </div>
